import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from '../views/layout/Layout'

export const constantRouterMap = [
  {
    path: '/login',
    // component: resolve => require(['../views/login/Login'], resolve),
    component: () => import('@/views/login/Login'),
    hidden: true
  },
  {
    path: '',
    component: Layout,
    redirect: 'dashboard',
    children: [{
      path: 'dashboard',
      component: () => import('@/views/dashboard/Dashboard.vue'),
      name: 'dashboard',
      meta: { title: 'Dashboard', icon: 'dashboard', noCache: true }
    }]
  },
  {
    path: '',
    component: Layout,
    redirect: 'document',
    children: [{
      path: 'document',
      component: () => import('@/views/document/Document.vue'),
      name: 'document',
      meta: { title: 'Document', icon: 'documentation', noCache: true }
    }]
  },
  {
    path: '/404',
    component: () => import('@/views/error-page/Error404'),
    hidden: true
  },
  {
    path: '/401',
    component: () => import('@/views/error-page/Error401'),
    hidden: true
  }
]

export default new Router({
  // mode: 'history', // require service support
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  {
    path: '/components',
    component: Layout,
    redirect: 'noredirect',
    name: 'components',
    meta: {
      title: 'Components',
      icon: 'component'
    },
    children: [
      { path: 'avatar-upload', component: () => import('@/views/components/ComAvatarUpload.vue'), name: 'avatarUpload-demo', meta: { title: 'AvatarUpload' }},
      { path: 'sticky', component: () => import('@/views/components/ComSticky.vue'), name: 'sticky-demo', meta: { title: 'Sticky' }},
      { path: 'back-to-top', component: () => import('@/views/components/ComBackToTop.vue'), name: 'backToTop-demo', meta: { title: 'BackToTop' }},
      { path: 'drag-kanban', component: () => import('@/views/components/ComDragKanban.vue'), name: 'dragKanban-demo', meta: { title: 'DragKanban' }}
    ]
  },
  {
    path: '/permission',
    component: Layout,
    redirect: 'noredirect',
    name: 'permission',
    alwaysShow: true, // will always show the root menu
    meta: {
      title: 'Permission',
      icon: 'lock'
      // roles: ['admin', 'editor']
    },
    children: [{
      path: 'admin',
      component: () => import('@/views/permission/PermissionAdmin.vue'),
      name: 'admin',
      meta: {
        title: 'Admin',
        roles: ['admin'] // or you can only set roles in sub nav
      }
    }, {
      path: 'editor',
      component: () => import('@/views/permission/PermissionEditor.vue'),
      name: 'editor',
      meta: {
        title: 'Editor',
        roles: ['editor']
      }
    }]
  },
  { path: '*', redirect: '/404', hidden: true }
]




