import { param2Obj } from '@/utils'

const userMap = {
  admin: {
    roles: ['admin'],
    token: 'admin',
    introduction: '我是超级管理员',
    avatar: 'https://cdn.yuque.com/yuque/0/2018/png/103452/1526285395445-assets/web-upload/937312e1-e80d-41b8-81f6-8c2b8373aee9.png',
    name: 'Super Admin'
  },
  editor: {
    roles: ['editor'],
    token: 'editor',
    introduction: '我是编辑',
    avatar: 'https://cdn.yuque.com/yuque/0/2018/png/103452/1526285401770-assets/web-upload/774e1391-6808-4fb5-8750-c2b6786dcf13.png',
    name: 'Normal Editor'
  }
}

export default {
  loginByUsername: config => {
    const { username } = JSON.parse(config.body)
    return userMap[username]
  },
  getUserInfo: config => {
    const { token } = param2Obj(config.url)
    if (userMap[token]) {
      return userMap[token]
    } else {
      return false
    }
  },
  logout: () => 'success'
}



